import React from "react";
import Typography from "@material-ui/core/Typography/Typography";
import CreateAnchorService from "../../services/create-anchor-service";

export default class DisplayH3 extends React.Component{

    render() {
        return (
            <Typography color="secondary" variant="h3" id={CreateAnchorService(this.props.content)}>
                {this.props.content}
            </Typography>
        )
    }
}